.section {
    position: relative;
    height: 100vh;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.parallax::after {
    content: " ";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;

    transform: translateZ(-1px) scale(1.5);
    background-size: cover;
    z-index: -1;
}

.bg1::after {
    background-image: url('../images/home.jpg');
    position: absolute;
    left: -5px;
}

.static {
    background-color: white;
    align-items: unset;
    justify-content: center;
}

.header {
    font-family: "Berlin Sans FB";
    font-size: 40px;
    font-weight: lighter;
    color: white;
}

.name {
    margin-top: 20px;
    font-family: "Berlin Sans FB";
    font-size: 100px;
    font-weight: bolder;
    color: white;
}